import React,{useState} from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import Slider from 'react-slick';

import "../../asset/slick/slick.css"; 
import "../../asset/slick/slick-theme.css";

import clickIcon from '../../asset/icon/detail_icon_white.png';
import emptyHeart from '../../asset/icon/empty_heart_icon.png';
import fullHeart from '../../asset/icon/full_heart_icon.png';

export default function imgDetail(){

    let buildingInfo = [
        {id:0,position:['20%','40%'],info:'천장재',state:false},
        {id:1,position:['69%','20%'],info:'바닥재',state:false},
        {id:2,position:['58%','80%'],info:'마감재',state:false},
    ];
    let [clickModal, setClickModal] = useState({id:'',position:[],info:'',state:false});
    let [heart,setHeart] = useState(false);

    function modalFunc(a, i){
        for(let x=0; x<buildingInfo.length; x++){
            buildingInfo.state = false;
        }
        a.state=true;
        setClickModal(a);
    }
    let sliderSetting = {
        dots:true,
        infinite:true,
        speed:500,
        slidesToShow:1,
        slidesToScroll:1
    }

    return(
        <div className="img_detail" >
            <div className="img_detail_container">
                {heart === false ?  <img src={emptyHeart} className="img_detail_like" onClick={()=>{setHeart(!heart);alert("해당 게시물 좋아요버튼을 누르셨습니다.");}}/> :  <img src={fullHeart} className="img_detail_like" onClick={()=>{setHeart(!heart)}}/>}
               

                <Slider {...sliderSetting}>
                    {[0,1,2].map((a, i)=>{
                        return(
                            <div className="img_position_box">
                                <img src={"/asset/img/aptpic/test-001.png"} className="test_img"/>
                                {
                                    buildingInfo.map((a,i)=>{
                                        return(
                                            <span className="click_point" style={{top:a.position[0], left:a.position[1]}} 
                                            onClick={()=>{modalFunc(a, i)}}><img src={clickIcon}/>
                                            </span>
                                        )
                                    })
                                }
                                {
                                clickModal.id !== '' ?
                                <div className="click_tooltip" style={{top:clickModal.position[0],left:clickModal.position[1]}}>
                                    {clickModal.position}입니다.
                                </div>:null
                                }
                            </div>
                        )
                    })
                    }
                </Slider>
            
            </div>
        </div>
    )
}