import React,{useState,useEffect} from 'react';
import {Link, useNavigate} from 'react-router-dom';
import '../../asset/all.css'
import '../../asset/aptsearch.css'

export default function AptSearch1(){
    let navigate = useNavigate();
    function goSearch(){
        if(nameSearch === ''){
            alert('아파트명을 입력해주세요.');
        }
        else{
            navigate("/search/"+nameSearch);
        }
    }
    function goSearch2(){
        if(cateSearch == false){
            alert('카테고리를 하나라도 선택해주세요.');
        }
        else{
            navigate("/search/"+cateSearch);
        }
    }
    let [nameSearch, setNameSearch]= useState('');
    let [cateSearch, setCateSearch] = useState([]);

    let selectOptions =[
        {id:0,name:'지역',option:['서울','인천','경기']},
        {id:1,name:'거래방식',option:['매매','전세','월세']},
        {id:2,name:'가격',option:['8억','9억','10억','11억']},
        {id:3,name:'면적',option:['20평대','30평대','40평대']}
    ];


    return(
        <div className="container">
            <div className="search_info">
                아파트이름 혹은 카테고리로<br/> 검색해 볼 수 있습니다.
            </div>
            <div className="search_container">
                    <p className="search_title">아파트 명으로 찾기</p>
                    <div className="search_name">
                        <input type="text" placeholder="ex) 송도 아메리칸 타운" onChange={(e)=>{setNameSearch(e.target.value);}}/>
                        <div className="btn_container">
                             {/* <Link to={"/search:"+nameSearch}><span className="btn_search">검  색</span></Link> */}
                             <span className="btn_search" onClick={goSearch}>검  색</span>
                        </div>
                    </div>
                    <p className="search_title">카테고리로 찾기</p>
                    <div className="search_category">
                        {selectOptions.map((a, i)=>{
                            return(
                                <div className="category cate">
                                    <span>{a.name}</span>
                                    <select className="category_box" onChange={(e)=>{setCateSearch(cateSearch=>[...cateSearch, e.target.value])}}>
                                        <option value="">{a.name}을 선택해주세요.</option>
                                        {
                                            selectOptions[i].option.map((a,i)=>{
                                                return(
                                                <option value={a}>{a}</option>
                                                )
                                            })
                                        }
                                    </select>
                                </div>
                            )
                        })}
                    </div>
                    <div className="btn_container">
                    <span className="btn_search" onClick={goSearch2}>검  색</span>
                    </div>
            </div>
        </div>
    );
}