import React,{useState} from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive'

import '../../asset/search.css'
import '../../asset/all.css'

 
export default function AptSearch2(){
  let navigate = useNavigate();


  function goDetail(){
    if(selectAptName === undefined) {alert("체크해주세요.");}
    else{
      navigate("detail/"+selectAptName);
    }
  }

   let {search} = useParams();
   let aptName = ["송도 레미안 어반파크","호반 써밋 화성 동탄 2지구","부평 힐스테이트 1지구"]
   let [selectAptName,setSelectAptName] = useState();
  //let {id} = useLocation();

  return(
  <div className="search_select">
    <div className="search_result_container">
      <span><strong>"{search}"</strong><br/>에 대한 검색결과입니다.</span>
    </div>
    <div className="search_select_container">
      {
        aptName.map((a,i)=>{
          return(
              <label htmlFor={"radio"+i} className="search_select_box">
                <span>{a}</span>
                <input type="radio" name="radio-select" value={a} id={"radio"+i} onChange={(e)=>{setSelectAptName(e.target.value)}}/>
              </label>
          )
        })
      }
    </div>
    <span className="btn_search" onClick={goDetail}>검  색</span>
  </div>
);
}
